
import { defineComponent, reactive, PropType, onMounted, computed } from 'vue'
import { emitter } from './ValidateForm.vue'

const emailReg = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
interface RuleProp {
    type: 'required' | 'email' | 'range' | 'custom'
    message?: string
    min?: {
        message: string
        length: number
    }
    max?: {
        message: string
        length: number
    }
    validator?: () => boolean
}
export type RulesProp = RuleProp[]
export type TagType = 'input' | 'textarea'

export default defineComponent({
    name: 'ValidateInput',
    props: {
        rules: Array as PropType<RulesProp>,
        modelValue: String,
        tag: {
            type: String as PropType<TagType>,
            default: 'input'
        }
    },
    inheritAttrs: false,
    setup(props, context) {
        const inputRef = reactive({
            val: computed({
                get: () => props.modelValue || '',
                set: (val) => {
                    context.emit('update:modelValue', val)
                }
            }),
            error: false,
            message: ''
        })
        const validataInput = () => {
            if (props.rules) {
                const allPassed = props.rules.every((rule) => {
                    let passed = true
                    if (rule.message) {
                        inputRef.message = rule.message
                    }
                    switch (rule.type) {
                        case 'required':
                            passed = inputRef.val.trim() !== ''
                            break
                        case 'email':
                            passed = emailReg.test(inputRef.val.trim())
                            break
                        case 'range':
                            if (rule.min) {
                                passed = !inputRef.val.includes(' ') && inputRef.val.length >= rule.min.length
                                inputRef.message = rule.min.message
                            }
                            if ((rule.max && passed) || (rule.max && inputRef.val.length > rule.max.length)) {
                                passed = !inputRef.val.includes(' ') && inputRef.val.length <= rule.max.length
                                inputRef.message = rule.max.message
                            }
                            break
                        case 'custom':
                            passed = rule.validator ? rule.validator() : true
                            break
                        default:
                            break
                    }
                    return passed
                })
                inputRef.error = !allPassed
                return allPassed
            }
            return true
        }
        onMounted(() => {
            emitter.emit('form-item-created', validataInput)
        })
        return {
            inputRef,
            validataInput
        }
    }
})
